  .card-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
    position: relative;
    border-radius: 50px;
    background: rgba(255, 255, 255, 0.1);
    overflow: hidden;
    justify-content: center;

  }
  .img-left,
  .img-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  @media screen and (max-width: 990px){
    .card-container {
      flex-direction: column;
    }
  }
  @media screen and (max-width: 550px){ 
    .card-right {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .img-left,
    .img-right {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

  }
  .card-container h3 {
    color: #000;
    font-size: 2rem;
    line-height: 2.4rem;
  }
  .card-container p {
        
      text-align: justify;
      margin-left: 0;
      margin-bottom: 0;
      word-wrap: break-word;
  }
    
    .card-right {
      width: 100%;
      align-items: right;
      justify-content: right;
      padding: 1.5rem;
    }
    @media screen and (max-width: 990px){
      .card-right{
        display: block;
        padding-top: 2rem;
      }
      .card-righ img {
        width: 80%;
        align-item: center;
      }
    }
    .card-right img {
      width: 80%;
    }

    .card-left {
      width: 100%;
      padding: 1.5rem;
          
    }
  .card-left h2 {
    padding-bottom: 1rem;
  }

       
    .u-col {
      margin-top: 2rem;
    }
    .u-container-layout {
        position: relative;
        
        flex: 1;
        max-width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        
    }
    .u-layout-row {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 2.5rem;

    } 
    .u-layout-row  span .u-icon-u {
      font-size: 5rem;
      color: red;
      width: 4rem;
    }
      @media screen and (max-width: 550px){
        .u-col .u-layout-row {
          display: grid;
          grid-template-columns: 1fr;
          grid-gap: 2.5rem;
        }
        .u-container-layout {
            position: relative;
            
            flex: 1;
            max-width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        .card-right {
          display: grid;
          grid-template-columns: 1fr;
          margin: 1rem;  
          width: 100%;
          align-items: center;
          justify-content: center;
          
        }
        .img-left {
          padding-bottom: 3rem;
        }

        
      }  















