.main-heading {
  font-size: 30px;
  line-height: 36px;
  font-weight: 700;
  margin: 0 0 60px;
  padding: 0 0 28px;
      padding-top: 0px;
  position: relative;
  z-index: 1;
}

.main-heading::before {
  position: absolute;
  content: "";
  z-index: -1;
  left: 50%;
  top: auto;
  bottom: 0;
  right: auto;
  transform: translateX(-50%);
  width: 70px;
  height: 4px;
}
  
